@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&amp;subset=cyrillic&display=swap);
script{position:absolute;width:1px;height:1px;overflow:hidden;clip:rect(0 0 0 0)}:root{--prime-color:      #3c8dbc;--prime-color-5l:   #4c99c6;--prime-color-10l:  #5fa4cc;--prime-color-5d:   #367fa9;--prime-color-10d:  #307095;--second-color:     #e3ecfd;--second-color-5l:  #fbfcff;--header-height: 40px}*{box-sizing:border-box;padding:0;margin:0}html,body,#app{height:100vh}body{font-family:Roboto, Arial, sans-serif}button,input,details summary,select{font:inherit;cursor:pointer}script{font-size:0}.header{display:flex;justify-content:center;align-items:center;height:40px;height:var(--header-height);position:relative;background-color:#3c8dbc;background-color:var(--prime-color)}.header ul{display:flex;align-items:center;position:absolute;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default}.header ul li{display:inline;list-style:none;margin:0 15px}.header--left{left:0}.header--right{right:0}.header button{background-color:transparent;border:0;color:#fff;cursor:pointer}.main{display:grid;height:calc(100% - 40px);height:calc(100% - var(--header-height));grid-template:'data data' -webkit-min-content 'time canvas' -webkit-max-content 'list canvas' auto / 250px auto;grid-template:'data data' min-content 'time canvas' max-content 'list canvas' auto / 250px auto}.main button{width:100%;padding:5px 10px;background-color:#4c99c6;background-color:var(--prime-color-5l);color:#fff;border:0}.main button span{margin-left:5px}.main__data{grid-area:data;display:flex;justify-content:space-between;align-items:center}.main__data form{width:25%;min-width:250px;padding:10px}.main__data form>*:nth-child(2){margin-top:2px}.main__time{grid-area:time;padding:10px}.main__time form label{margin-right:1px}.main__time form select{background-color:#e3ecfd;background-color:var(--second-color);color:#000;border:1px solid #367fa9;border:1px solid var(--prime-color-5d)}.main__time form>*:nth-child(2){margin-top:2px}.main__list{grid-area:list;padding:5px 10px;overflow-y:scroll}.main__list ul li{list-style:none;margin-top:5px}.main__list ul li ul{margin-left:15px}.main__list button{width:auto;border:1px solid #3c8dbc;border:1px solid var(--prime-color)}.main__canvas{grid-area:canvas;display:flex;justify-content:center;align-items:center}.main>*{border:1px solid #ccc}.inputs[data-flex]{display:flex}.inputs[data-flex] label{display:flex;align-items:center}.inputs[data-flex] input[type='text']{width:100%}.inputs .select-wrapper{position:relative;width:100%}.inputs .select-wrapper select{background-color:#4c99c6;background-color:var(--prime-color-5l);-webkit-appearance:none;-moz-appearance:none;appearance:none;position:relative;width:100%;padding:5px 10px;border:0;color:#fff;text-align-last:center}.inputs .select-wrapper select:disabled{background-color:#3c8dbc;background-color:var(--prime-color);cursor:not-allowed}.inputs .select-wrapper select+.fa{position:absolute;top:5px;right:5px;color:#fff}.input-group:not([class$='--column'])>*{vertical-align:middle;display:inline-block}.input-group label{background-color:#4c99c6;background-color:var(--prime-color-5l);color:#fff;padding:4px 10px}.input-group input[type='text']{padding:5px 10px;border:1px solid #4c99c6;border:1px solid var(--prime-color-5l);background-color:#e3ecfd;background-color:var(--second-color)}.input-group input[type='checkbox']{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#4c99c6;background-color:var(--prime-color-5l);width:100%;padding:6px 10px;border:1px solid #3c8dbc;border:1px solid var(--prime-color);color:#fff;text-align:center}.input-group input[type='checkbox']:checked{background-color:#367fa9;background-color:var(--prime-color-5d)}.input-group button{border:1px solid #3c8dbc;border:1px solid var(--prime-color)}.input-group--column{width:100%}

