@mixin reset {
  * {
    box-sizing: border-box;
    padding: 0; margin: 0;
  }
}

@mixin transition($sec) {
  transition: all $sec ease-in-out;
}

@mixin flexbox($direction, $justify, $align-items, $align-content) {
  display: flex;
  @if $direction != auto { flex-direction: $direction }
  @if $justify != auto { justify-content: $justify }
  @if $align-items != auto { align-items: $align-items }
  @if $align-content != auto { align-content: $align-content }
}

@mixin inline-middle {
  vertical-align: middle;
  display: inline-block;
}
